

export interface DraaiSessieJson{
    id: number
    startUur: string,
    duur: string,
    evenementId: number,
    gebruikerId: number,
}

export class DraaiSessie {
    private _id: number
    private _evenementId: number
    private _gebruikerId: number
    constructor(
        private _startUur:string,
        private _duur: string,
    )
    {}
    get id() {return this._id}
    get startUur(){return this._startUur}
    get duur() {return this._duur}
    get evenementId(){return this._evenementId}
    get gebruikerId(){return this._gebruikerId}

    set id(id:number) { this._id = id}
    set startUur(startUur: string){this._startUur = startUur}
    set duur(duur: string){this._duur = duur}
    set evenementId(evenementId: number){this._evenementId = evenementId}
    set gebruikerId( gebruikerId: number){this._gebruikerId = gebruikerId}

    static fromJSON(json: DraaiSessieJson): DraaiSessie {
        const rec = new DraaiSessie(json.startUur, json.duur)
        rec._id = json.id;
        rec._evenementId = json.evenementId;
        rec._gebruikerId = json.gebruikerId;
        return rec;
      }

     toJSON(): DraaiSessieJson {
        return <DraaiSessieJson>{
            startUur: this.startUur,
            duur: this.duur,
        }
    }
}


