import { Injectable } from '@angular/core';
import { Gebruiker } from '../models/gebruiker.model';
import { BehaviorSubject, Observable, throwError } from 'rxjs';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { map, catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class GebruikerDataService {

  private _gebruikers$ = new BehaviorSubject<Gebruiker[]>([]);
  private _gebruikers: Gebruiker[];
  constructor(private http: HttpClient) {
    this.gebruikers$.subscribe((gebruikers: Gebruiker[])=>{
      this._gebruikers = gebruikers;
      this._gebruikers$.next(this._gebruikers);
    })
   }

  get allGebruikers$(): Observable<Gebruiker[]>{
    return this._gebruikers$;
  }
  get gebruikers$(): Observable< Gebruiker[] >{
    return this.http.get(`${environment.apiUrl}/Gebruiker/`).pipe(
      catchError(this.handleError),
      map(
        (list: any[]) : Gebruiker[] => list.map(Gebruiker.fromJSON))
    );
  }
  handleError(err: any): Observable<never> {
    let errorMessage: string;
    if (err instanceof HttpErrorResponse) {
      errorMessage = `'${err.status} ${err.statusText}' when accessing '${err.url}'`;
    } else {
      errorMessage = `an unknown error occurred ${err}`;
    }
    console.error(err);
    return throwError (errorMessage);
  }

  getGebruiker$(id: number): Observable<Gebruiker> {
    return this.http
      .get(`${environment.apiUrl}/Gebruiker/${id}`)
      .pipe(catchError(this.handleError), map(Gebruiker.fromJSON));
  }
}
