import { Pipe, PipeTransform } from '@angular/core';
import { Evenement } from '../models/evenement.model';

@Pipe({
  name: 'evenementFilter'
})
export class EvenementFilterPipe implements PipeTransform {

  transform(evenementen: Evenement[], name: string): Evenement[] {
    if(!name || name.length === 0){
      return evenementen;
    }
    return evenementen.filter(ev => ev.naam.toLowerCase().startsWith(name.toLowerCase()));
  }

}
