import { Component, OnInit } from '@angular/core';
//import {EVENEMENTEN} from '../../../mock-evenementen';
import { EvenementDataService } from 'src/app/data-services/evenement-data.service';
import { Subject, Observable, EMPTY } from 'rxjs';
import { distinctUntilChanged, debounceTime,
  map, filter, catchError } from 'rxjs/operators';
import { Evenement } from 'src/app/models/evenement.model';
@Component({
  selector: 'app-evenement-list',
  templateUrl: './evenement-list.component.html',
  styleUrls: ['./evenement-list.component.css']
})
export class EvenementListComponent implements OnInit {
  private _fetchEvenementen$: Observable<Evenement[]>
  public errorMessage: string = '';
  private _evenementen: Evenement[];
  public filterEvenementName: string;
  public filterEvenement$ = new Subject<string>();
  applyFilter(filter: string){
    this.filterEvenementName =filter;
  }

  constructor(private _evenementDataService: EvenementDataService) { 
    
    this.filterEvenement$.pipe(distinctUntilChanged(), debounceTime(400), 
    map(val => val.toLocaleLowerCase()), 
    filter(val => !val.startsWith('s'))).subscribe(val => this.filterEvenementName = val);
  }

get evenementen$(): Observable<Evenement[]>{
  return this._fetchEvenementen$;
}/*
addNewEvenement(evenement){
  this._evenementDataService.addNewEvenement(evenement);
}*/
  ngOnInit(): void {
    this._fetchEvenementen$ = this._evenementDataService.evenementen$.pipe(catchError (err=>{
      this.errorMessage = err;
      return EMPTY;
    })
    );
  }

}
