
interface GebruikerJson{
    naam: string
    alias: string,
    email: string,
    id: number,
}

export class Gebruiker {
    private _id: number
    constructor(
        private _naam:string,
        private _alias: string,
        private _email: string,
    )
    {}
    get id() {return this._id}
    get naam(){return this._naam}
    get alias() {return this._alias}
    get email(){return this._email}

    set id(id:number) { this._id = id}
    set naam(naam: string){this._naam = naam}
    set alias(alias: string){this._alias = alias}
    set email(email: string){this._email = email}


    static fromJSON(json: GebruikerJson): Gebruiker {
        const rec = new Gebruiker(json.naam, json.alias, json.email)
        rec._id = json.id;
        return rec;
      }
}


