import { Component, OnInit } from '@angular/core';
import { Evenement } from 'src/app/models/evenement.model';
import { ActivatedRoute } from '@angular/router';
import { EvenementDataService } from 'src/app/data-services/evenement-data.service';


@Component({
  selector: 'app-evenement-detail',
  templateUrl: './evenement-detail.component.html',
  styleUrls: ['./evenement-detail.component.css']
})
export class EvenementDetailComponent implements OnInit {
  public evenement: Evenement;
  constructor(private route: ActivatedRoute, private _evenementDataService: EvenementDataService) { }

  ngOnInit(): void {
    this.route.data.subscribe(item => this.evenement = item['evenement']);
  }

}
