import { Injectable } from '@angular/core';
import {EVENEMENTEN} from '../mock-evenementen';
import { Evenement } from '../models/evenement.model';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Observable, throwError, BehaviorSubject } from 'rxjs';
import { map, delay, catchError } from 'rxjs/operators';
@Injectable({
  providedIn: 'root'
})
export class EvenementDataService {
private _evenementen$ = new BehaviorSubject<Evenement[]>([]);
private _evenementen: Evenement[];
  constructor(private http: HttpClient) {
    this.evenementen$.subscribe((evenementen: Evenement[])=>{
      this._evenementen = evenementen;
      this._evenementen$.next(this._evenementen);
    })
   }

  get allEvenementen$(): Observable<Evenement[]>{
    return this._evenementen$;
  }
  get evenementen$(): Observable< Evenement[] >{
    return this.http.get(`${environment.apiUrl}/Evenement/`).pipe(
      catchError(this.handleError),
      map(
        (list: any[]) : Evenement[] => list.map(Evenement.fromJSON))
    );
  }
  handleError(err: any): Observable<never> {
    let errorMessage: string;
    if (err instanceof HttpErrorResponse) {
      errorMessage = `'${err.status} ${err.statusText}' when accessing '${err.url}'`;
    } else {
      errorMessage = `an unknown error occurred ${err}`;
    }
    console.error(err);
    return throwError (errorMessage);
  }
  
  addNewEvenement(evenement: Evenement){
    return this.http.post(`${environment.apiUrl}/Evenement/`, evenement.toJSON())
    .pipe(catchError(this.handleError), map(Evenement.fromJSON)).subscribe();  
  }
  getEvenement$(id: string): Observable<Evenement> {
    return this.http
      .get(`${environment.apiUrl}/Evenement/${id}`)
      .pipe(catchError(this.handleError), map(Evenement.fromJSON));
  }
  deleteEvenement(id: number) : Observable<Evenement>{
    return this.http.delete(`${environment.apiUrl}/Evenement/${id}`)
    .pipe(map(
      (evenenmentJSON: any): Evenement => Evenement.fromJSON(evenenmentJSON)
    ));
  }


}
