import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {DraaiSessieComponent} from '../draai-sessie/draai-sessie.component';
import {EvenementComponent} from './evenement.component';
import {MaterialModule} from '../../material/material.module';
import { EvenementListComponent } from './evenement-list/evenement-list.component';
import { EvenementFilterPipe } from '../../filters/evenement-filter.pipe';
import { ReactiveFormsModule} from '@angular/forms';
import { AddEvenementComponent } from './add-evenement/add-evenement.component';
import { EvenementDetailComponent } from './evenement-detail/evenement-detail.component';
import {EvenementResolver} from './EvenementResolver';
import { RouterModule, Routes } from '@angular/router';
import { RemoveEvenementComponent } from './remove-evenement/remove-evenement.component';

const routes: Routes = [
  {path: 'list', component: EvenementListComponent},
  {path: "add", component: AddEvenementComponent},
  {
    path: "detail/:id",
     component: EvenementDetailComponent, 
     resolve: {evenement: EvenementResolver},
    },

    {path: "verwijder/:id", component: RemoveEvenementComponent, resolve: {evenement: EvenementResolver},}
    //{path: '', pathMatch:"full", redirectTo:"list"}
];

@NgModule({
  declarations: [
    EvenementComponent, 
    DraaiSessieComponent,
    EvenementListComponent,
    AddEvenementComponent, 
    EvenementFilterPipe, 
    EvenementDetailComponent, RemoveEvenementComponent],
  imports: [
    CommonModule,
    MaterialModule,
    ReactiveFormsModule,
    RouterModule.forChild(routes),
  ], 
  exports: [EvenementListComponent, AddEvenementComponent, EvenementDetailComponent], 
})
export class EvenementModule { }
