import { Component, OnInit, Input } from '@angular/core';
import { DraaiSessie} from '../../models/draai-sessie.model'
import { GebruikerDataService } from '../../data-services/gebruiker-data.service';
import { Gebruiker } from '../../models/gebruiker.model';
import { Observable, Subject } from 'rxjs';
import { distinctUntilChanged, debounceTime, map } from 'rxjs/operators';

@Component({
  selector: 'app-draai-sessie',
  templateUrl: './draai-sessie.component.html',
  styleUrls: ['./draai-sessie.component.css']
})
export class DraaiSessieComponent implements OnInit {
@Input() public draaiSessie: DraaiSessie;
          public gebruiker: Gebruiker;
  constructor( private gebruikerDataService: GebruikerDataService) {
    
   }

  ngOnInit(): void {
    this.gebruikerDataService.getGebruiker$(this.draaiSessie.gebruikerId).subscribe((val:Gebruiker) => (this.gebruiker = val));
    console.log(this.gebruiker);
  }

}
