import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder, FormArray } from '@angular/forms';
import { Evenement } from '../../../models/evenement.model';
import { DraaiSessie } from '../../../models/draai-sessie.model';
import { EvenementDataService } from '../../../data-services/evenement-data.service';

@Component({
  selector: 'app-add-evenement',
  templateUrl: './add-evenement.component.html',
  styleUrls: ['./add-evenement.component.css']
})
export class AddEvenementComponent implements OnInit {
  @Output() public newEvenement = new EventEmitter<Evenement>();
  public evenement: FormGroup;
  constructor(
    private fb: FormBuilder,
    private _evenementDataService: EvenementDataService
    ) { }

  ngOnInit(): void {
    this.evenement = this.fb.group({
      naam: this.fb.control('naam', [Validators.required, Validators.minLength(2)]),
      datum: this.fb.control('datum', Validators.required),
      locatie: this.fb.control('locatie', Validators.required),
      startUur: this.fb.control('startUur', Validators.required),
      duur: this.fb.control('duur', Validators.required),
      //startUurDSessie: this.fb.control('startUurDSessie', Validators.required),
      //duurDSessie: this.fb.control('duurDSessie', Validators.required),
    })
  }
  onSubmit(){
    /*console.log(this.evenement.value);
    var startUur: number  = parseInt(this.evenement.value.startUurDSessie);
    var duur:number = parseInt(this.evenement.value.duurDSessie)
    //let draaiSessie:number[] = [startUur, duur];
    let draaiSessies = new Array<DraaiSessie>();
    draaiSessies.push(new DraaiSessie(this.evenement.value.startUurDSessie, this.evenement.value.duurDSessie));*/
    this._evenementDataService.addNewEvenement(new Evenement(this.evenement.value.naam, new Date(this.evenement.value.datum), this.evenement.value.locatie, this.evenement.value.startUur, this.evenement.value.duur))
    this.evenement.reset();
    
  }

  addEvenement(naam: HTMLInputElement, datum: HTMLInputElement, locatie: HTMLInputElement, startUur: HTMLInputElement, duur: HTMLInputElement){
    console.log(naam.value);
    //const draaiSessie= new DraaiSessie(startUurDraaiSessie.value, duurDraaiSessie.value);
    const evenement = new Evenement(naam.value, new Date(datum.value), locatie.value, startUur.value, duur.value );
    this.newEvenement.emit(evenement);
  }

  getErrorMessage(errors:any): string{
    if(errors.required){
      return 'mag niet leeg zijn!'
    }
    else if(errors.minLength){
      return `Heeft minstens ${errors.minLength.requiredLength} karakters (got ${errors.minLength.actualLength})` ;
    }
  }

  get draaiSessies(): FormArray{
    return this.evenement.get('draaiSessies') as FormArray;
  }

  getDraaiSessie(index: number): FormGroup{
   
    return this.draaiSessies.controls[index] as FormGroup;
  }

}
