import { NgModule } from '@angular/core';
import { Routes, RouterModule, PreloadAllModules } from '@angular/router';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { EvenementListComponent } from './components/evenement/evenement-list/evenement-list.component';
import { AddEvenementComponent } from './components/evenement/add-evenement/add-evenement.component';
import { EvenementDetailComponent } from './components/evenement/evenement-detail/evenement-detail.component';
import { AuthGuard } from './users/user/auth.guard';
import { CommonModule } from '@angular/common';
import { EvenementModule } from './components/evenement/evenement.module';
import { LoginComponent } from './users/user/login/login.component';
import { RegisterComponent } from './users/user/register/register.component';
import { PlaylistComponent } from './components/playlist/playlist.component'


const appRoutes: Routes = [
  
  /*{ path: 'evenementen/list', component: EvenementListComponent },
  { path: 'evenementen/add', component: AddEvenementComponent },
  { path: 'evenementen/detail/:id', component: EvenementDetailComponent},
  { path: '', redirectTo: 'evenementen/list', pathMatch: 'full'},*/
  {
    path: 'evenementen',
    canActivate: [AuthGuard],
    loadChildren: () => import ('./components/evenement/evenement.module').then(mod => mod.EvenementModule)
  },
  {path: 'playlist', component: PlaylistComponent},
  { path: 'login', component: LoginComponent },
  {path: 'register', component: RegisterComponent},
  { path: '', redirectTo: 'login', pathMatch: 'full'}
,  { path: '**', component: PageNotFoundComponent},
];

@NgModule({
  imports: [CommonModule, EvenementModule, RouterModule.forRoot(appRoutes, {preloadingStrategy: PreloadAllModules})
  ],
  declarations: [],
  exports: [RouterModule]
})
export class AppRoutingModule { }
