import { DraaiSessie, DraaiSessieJson } from './draai-sessie.model';
interface EvenementJson {
    id: number
    naam: string;
    draaiSessies: DraaiSessieJson[];
    datum: string;
    locatie: string;
    startUur: string;
    duur: string;
  }
  export class Evenement {
    
    private _id: number;
    private _draaiSessies= new Array<DraaiSessie>();
    constructor(
      private _naam: string,
      private _datum: Date,
      private _locatie: string,
      private _startUur: string,
      private _duur: string
    ) {}

    get id() {return this._id}
    get naam() { return this._naam; }
    get draaiSessies() {return this._draaiSessies}
    get datum() {return this._datum}
    get locatie() {return this._locatie}
    get startUur() {return this._startUur}
    get duur() {return this._duur}
 
    set id(id: number) {this._id = id}
    set naam(naam: string) { this._naam = naam; }
    set draaiSessies(draaiSessies: Array<DraaiSessie>) { this._draaiSessies = draaiSessies; }
    set datum(datum: Date) { this._datum = datum}
    set locatie(locatie: string){ this._locatie = locatie}
    set startUur(startUur: string){this._startUur = startUur}
    set duur (duur: string){this._duur=duur}

  
    static fromJSON(json: EvenementJson): Evenement {
      const rec = new Evenement(json.naam, new Date(json.datum), json.locatie, json.startUur, json.duur);
      rec._draaiSessies = json.draaiSessies.map(DraaiSessie.fromJSON);
      rec._id = json.id;
      return rec;
    }

    toJSON(): any {
      return {
      naam: this.naam,
      draaiSessies: this.draaiSessies.map(d => d.toJSON()),
      datum: this.datum,
      locatie: this.locatie,
      startUur: this.startUur,
      duur: this.duur,
    }
    }
    addIngredient(startuur: string, duur: string) {
      this._draaiSessies.push(new DraaiSessie(startuur, duur,));
    }
    // [...] other getters
  }