import {
    Resolve,
    RouterStateSnapshot,
    ActivatedRouteSnapshot
  } from '@angular/router';
  import { Evenement } from "../../models/evenement.model";
  import { Injectable } from '@angular/core';
  import { EvenementDataService } from '../../data-services/evenement-data.service';
  import { Observable } from 'rxjs';
  
  @Injectable({
    providedIn: 'root'
  })
  export class EvenementResolver implements Resolve<Evenement> {
    constructor(private evenementService: EvenementDataService) {}
  
    resolve(
      route: ActivatedRouteSnapshot,
      state: RouterStateSnapshot
    ): Observable<Evenement> {
      return this.evenementService.getEvenement$(route.params['id']);
    }
  }